___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  font-family: "poppins", sans-serif;
  overflow-x: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
}

main {
  flex: 1;
}

.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
  height: 44px;
  width: 44px;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  color: #468585;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #468585;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
  height: 44px;
  width: 134px;
}


main {
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}

.fan-art-resto {
  width: 100%;
  max-height: 450px;
  object-fit: cover;
  object-position: center;
}

.resto-info {
  padding: 16px 24px;
}

.resto-info__title h2 {
  font-weight: lighter;
  text-align: center;
}

.resto-info__description p {
  display: -webkit-box;
  margin-top: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  text-align: justify;
}

.resto-info__city p {
  font-size: 1rem;
  color: #555;
  margin: 5px 0;
  font-weight: bold;
}

.resto-info__rating p {
  display: inline-flex;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  color: #FFD700;
  margin: 5px 0;
}

.resto-info__rating p::before {
  content: "★";
  color: #FFD700;
  margin-right: 5px;
}

.resto__name {
  font-size: 2em;
  font-weight: bold;
  color: #468585;
  margin-bottom: 20px;
  text-align: center;
}

.resto__image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
}

.resto__info {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(29, 27, 169, 0.384);
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 30px;
}

.resto__info h3 {
  font-size: 1.5em;
  color: #468585;
  margin-bottom: 15px;
  text-align: center;
}

.resto__info h4 {
  font-size: 1.25em;
  color: #555;
  margin-bottom: 10px;
}

.resto__info p {
  font-size: 1em;
  color: #777;
  margin-bottom: 15px;
  text-align: justify;
}

.resto__menu {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(29, 27, 169, 0.384);
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 30px;
  display: flex; 
  flex-wrap: wrap;
  gap: 20px; 
}

.resto__menu h3 {
  font-size: 1.5em;
  color: #468585;
  margin-bottom: 15px;
  text-align: center;
  width: 100%;
}

.resto__menu > div {
  flex: 1; 
  min-width: 200px; 
}

.resto__menu h4 {
  font-size: 1.25em;
  color: #555;
  margin-bottom: 10px;
}

.resto__menu ul {
  list-style-type: none;
  padding: 0;
}

.resto__menu ul li {
  font-size: 1em;
  color: #777;
  margin-bottom: 5px;
}

.resto__menu ul li::before {
  content: "•";
  margin-right: 10px;
  color: #468585;
}


.resto__reviews {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(29, 27, 169, 0.384);
  padding: 20px;
  border-radius: 8px;
}

.resto__reviews h3 {
  font-size: 1.5em;
  color: #468585;
  margin-bottom: 15px;
  text-align: center;
}

.review {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
}

.review h4 {
  font-size: 1.25em;
  font-weight: bold;
  color:  #555;
}

.review p {
  font-size: 1em;
  color: #777;
  margin-top: 10px;
}

.review p:last-child {
  margin-top: 10px;
  font-weight: bold;
  color: #777;
}

.resto-info__button {
  margin-top: 15px;
  display: flex;
  justify-content: flex-start; 
}

.view-detail-button {
  padding: 10px 5px; 
  background-color: #468585; 
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 5px; 
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  height: 44px;
  width: 100px;
}

.view-detail-button:hover {
  background-color: #3d6c6c; 
  transform: translateY(-3px); 
}

.view-detail-button:focus {
  outline: none;
}

.view-detail-button:active {
  background-color: #3d6c6c; 
}



.back-button {
  background-color: #468585; 
  color: white; 
  border: none; 
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 16px; 
  cursor: pointer; 
  transition: background-color 0.3s ease; 
  height: 44px;
  width: 80px;
}

.back-button:hover {
  background-color: #3d6c6c; 
}

.hero {
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding-top: 0%;
}

.hero img {
  width: 100%;
  max-width: 100%;
  height: auto;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: white;
  cursor: pointer;
}

footer {
  padding: 24px 20px;
  background-color: #468585;     
  text-align: center;
  color: white;
}

.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-to-content:focus {
  top: 0;
}

@media (max-width: 768px) {
  .resto__name {
    font-size: 1.75em;
  }

  .resto__image {
    max-width: 100%;
    max-height: 300px;
  }

  .resto__info, .resto__menu, .resto__reviews {
    margin-bottom: 20px;
  }

  .resto__info h3, .resto__menu h3, .resto__reviews h3 {
    font-size: 1.25em;
  }

  .resto__info h4, .resto__menu h4 {
    font-size: 1.125em;
  }
}

@media screen and (min-width: 650px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }

  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }

  .app-bar .app-bar__menu {
    display: none;
  }

  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }

  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }

  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }
}

@media screen and (min-width: 800px) {
  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
}

@media (min-width: 1200px) {
  .hero img {
    width: 1000px;
  }
}




